import Vue from 'vue'

import {
  Vuetify,
  VApp,
  VNavigationDrawer,
  VFooter,
  VList,
  VCard,
  VBtn,
  VIcon,
  VGrid,
  VToolbar,
  VCarousel,
  VParallax,
  transitions
} from 'vuetify'

import './css/config.styl'
import './css/main.css'

import App from './App'
import Countdown from './components/Countdown'
import router from './router'

Vue.component('countdown', Countdown)

Vue.use(Vuetify, {
  components: {
    VApp,
    VNavigationDrawer,
    VFooter,
    VList,
    VCard,
    VBtn,
    VIcon,
    VGrid,
    VToolbar,
    VCarousel,
    VParallax,
    transitions
  },
  theme: {
    primary: 'rgb(47, 61, 138)',
    secondary: '#424242',
    accent: '#82B1FF',
    error: '#FF5252',
    info: '#2196F3',
    success: '#4CAF50',
    warning: '#FFC107'
  }
})

Vue.config.productionTip = false

Vue.filter('two_digits', function (value) {
  if (value.toString().length <= 1) {
    return '0' + value.toString()
  }
  return value.toString()
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



