<template>
  <v-layout row>
    <v-flex xs12 class="text-xs-center">
      <div class="block">
        <p class="digit">{{ days | two_digits }}</p>
        <p class="text">Days</p>
      </div>
    </v-flex>
    <v-flex xs12 class="text-xs-center">
      <div class="block">
        <p class="digit">{{ hours | two_digits }}</p>
        <p class="text">Hours</p>
      </div>
    </v-flex>
    <v-flex xs12 class="text-xs-center">
      <div class="block">
        <p class="digit">{{ minutes | two_digits }}</p>
        <p class="text">Minutes</p>
      </div>
    </v-flex>
    <v-flex xs12 class="text-xs-center">
      <div class="block">
        <p class="digit">{{ seconds | two_digits }}</p>
        <p class="text">Seconds</p>
      </div>
    </v-flex>
  </v-layout>
</template>
<script>
  export default {
    name: 'Countdown',
    props: {
      date: {
        type: String
      }
    },
    data () {
      return {
        now: Math.trunc((new Date()).getTime() / 1000)
      }
    },
    mounted () {
      setInterval(() => {
        this.$data.now = Math.trunc((new Date()).getTime() / 1000)
      }, 1000)
    },
    computed: {
      modifiedDate () {
        return Math.trunc(Date.parse(this.date) / 1000)
      },
      seconds () {
        return (this.modifiedDate - this.now) % 60
      },
      minutes () {
        return Math.trunc((this.modifiedDate - this.now) / 60) % 60
      },
      hours () {
        return Math.trunc((this.modifiedDate - this.now) / 60 / 60) % 24
      },
      days () {
        return Math.trunc((this.modifiedDate - this.now) / 60 / 60 / 24)
      }
    }
  }
</script>
<style>
  .block {
    display: flex;
    flex-direction: column;
    margin: 20px;
  }
  .text {
    /* color: #1abc9c; */
    color: rgb(100, 116, 203);
    text-shadow: 1px 1px 1px rgba(0,0,0,.7);
    font-size: 35px;
    font-family: 'Roboto Condensed', serif;
    font-weight: 400;
    margin-top: 10px;
    margin-bottom: 10px;
    text-align: center;
    line-height: 40px;
  }
  .digit {
    color: #ecf0f1;
    font-size: 100px;
    font-weight: 100;
    font-family: 'Roboto', serif;
    margin: 10px;
    text-align: center;
    line-height: 100px;
  }

  @media screen and (max-width: 599px) {
    .block {
      margin: 10px;
    }
    .text {
      font-size: 20px;
      line-height: 25px;
    }
    .digit {
      font-size: 50px;
      line-height: 50px;
    }
  }
</style>


