<template>
  <v-app dark>
    <v-navigation-drawer dark
      fixed
      v-model="drawer"
      persistent
      app
    >
      <v-list>
        <v-list-tile
          v-for="(item, i) in items" :key="i" value="true"
          class="color-white"
        >
          <v-list-tile-action v-if="item.icon">
            <v-icon v-html="item.icon"></v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="item.title"></v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar dark app fixed flat class="primary" v-bind:class="{ 'is-top': isTop }">
      <v-toolbar-side-icon class="hidden-md-and-up" @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title class="clickable">
        <img src="/static/logo-small.png" class="logo" alt="Blockform" />
      </v-toolbar-title>
      <v-toolbar-title class="clickable text-uppercase">
        {{ title }}
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn
          flat
          v-for="(item, i) in items"
          :key="i"
          value="true">
          {{ item.title }}
        </v-btn>
      </v-toolbar-items>
      <v-btn light class="grey lighten-4">
        Login
      </v-btn>
    </v-toolbar>
    <v-content class="content-no-pd-top">
      <!-- #Carousel -->
      <v-layout row>
        <v-flex xs12>
          <v-carousel delimiter-icon="remove" class="carousel-full-height" hide-delimiters>
            <v-carousel-item src="/static/map.jpg">
              <div class="carousel-text">
                <h2 class="mb-3 text-xs-center">THE BLOCFORM COIN</h2>
                <div class="mb-2 text-xs-center">
                  <img src="/static/logo.png" class="intro-logo" alt="Blockform" />
                </div>
                <p>All of the investors Travellers Fans comunities around the world buy and use the B-coin like a smart solution for Online Payment. 
                We offer all of you a special opportunity to have the Luxurious and Intelligent Living with B-Coin.</p>
              </div>
            </v-carousel-item>
            <v-carousel-item src="/static/map.jpg">
              <div class="carousel-text">
                <h2 class="mb-3">THE ADVANTAGES AND DIFFERENCES OF BLOC FORM</h2>
                <ul>
                  <li>We have over 130 Millions of Membership around the world.</li>
                  <li>We have many Exclusive Technology Platforms</li>
                  <li>We have A hundred of million Fans in many countries</li>
                  <li>We have many Potential projects like smart city, Smart School, FIIm Prodution...ect.</li>
                  <li>We have the network of Agency in many countries such as US, Asia, Europe Market.</li>
                  <li>Especially, The Blocform's policy ready to share the Profit to all Investors with IPO plan.</li>
                </ul>
              </div>
            </v-carousel-item>
          </v-carousel>
        </v-flex>
      </v-layout><!-- #End Carousel -->
      <!-- #Countdown -->
      <v-layout row align-center pt-5 class="bg-dark-blue">
        <v-flex xs12 md6 offset-md3 class="text-xs-center">
          <h1 class="big-title">BLOCFORM Pre Sale will end on<br/>
            December 31th, 23:59 PM. GMT</h1>
        </v-flex>
      </v-layout>
      <v-layout row align-center pb-5 class="bg-dark-blue">
        <v-flex xs12 md6 offset-md3 class="text-xs-center">
          <countdown date="2017-12-31T15:59:59.000Z"></countdown>
        </v-flex>
      </v-layout><!-- #End Countdown -->
      <!-- #About us -->
      <v-layout row wrap>
        <v-flex d-flex xs12 sm6>
          <v-layout>
            <v-flex xs12>
              <v-parallax src="/static/coin-net.jpg"></v-parallax>
            </v-flex>
          </v-layout>
        </v-flex>
        <v-flex light d-flex xs12 sm6 class="text-xs-center grey lighten-4 color-dark" pa-5>
          <transition name="fade" mode="in-out">
            <div class="font-2 font-size-md text-xs-center" v-if="animationShow">
            <h1 class="mb-3 color-primary">Blocform (B)</h1>
            <div class="mb-2 text-xs-center">
              <img src="/static/logo-blue.png" class="intro-logo" alt="Blockform" />
            </div>
            <p>Blocform (B) is a self-managing financial system with P2P transaction based on open source platform.
              This currency is like the Dollar, but it exists only in the digital world. And it's developed on the Ethereum token ERC20 and Blocform (B) running on a Ethereum blockchain.</p>
          </div>
          </transition>
        </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex light d-flex xs12 sm6 class="grey lighten-4 color-dark" pa-4>
          <div class="font-2 font-size-sm text-xs-center">
            <h1 class="mb-2 color-primary text-xs-center">Vision</h1>
            <div class="mb-2 text-xs-center">
              <img src="/static/vision.png" class="intro-logo-small" alt="Blockform" />
            </div>
              <p>Redefining Currency: Blocform (B) will be defined by three core trends at the moment:</p>
              <p>1. Cash remains the dominant force in human life, especially in developing economies, and that will not change in the next 5 to 10 years.</p>
              <p>2. In the future, technology can allow development to meet the demand for new currencies.</p>
              <p>3. Electronic money is becoming more and more popular. Typically Bitcoin, Ethereum ... are being accepted in countries such as Japan and European Union countries, in particular European Court of Justice has accepted electronic money as a kind of currency. Even European banks have also accepted this form of payment</p>
          </div>
        </v-flex>
        <v-flex d-flex xs12 sm6>
          <v-layout>
            <v-flex xs12>
              <v-parallax src="/static/our-vision-1.jpg" jumbotron></v-parallax>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex d-flex xs12 sm6>
          <v-layout>
            <v-flex xs12>
              <v-parallax src="/static/our-vision-5.jpg" jumbotron></v-parallax>
            </v-flex>
          </v-layout>
        </v-flex>
        <v-flex light d-flex xs12 sm6 class="text-xs-center grey lighten-4 color-dark" pa-4>
          <div class="font-2 font-size-sm text-xs-center">
            <h1 class="mb-1 color-primary">Smart contract</h1>
            <div class="mb-2 text-xs-center">
              <img src="/static/mission.png" class="intro-logo-small" alt="Blockform" />
            </div>
            <ul class="pl-3">
              <li class="mb-2">The main objective of a smart contract is to allow the two parties to work and transact on the internet without intermediaries. Applications work exactly like programmers without any downtime. The whole process of the smart contract is done automatically without intervention by the third party. We may publicly disclose those transactions but we cannot know who made them.</li>
              <li>Safety Blocform (B) is not controlled by any organizations or governments. Blocform (B) uses blockchain technology and the database is continuously recorded and shared to all servers, so all information is impossible to be tampered or cheated. If someone wants to cheat, they must hack all the servers at the same time.</li>
            </ul>
          </div>
        </v-flex>
      </v-layout><!-- #End About us -->
    </v-content>
    <v-footer app class="py-4">
      <v-layout row>
        <v-flex xs12 class="text-xs-center">
          <span>Copyrights &copy; Blockform 2017</span>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>

<script>
  export default {
    methods: {
      handleScroll () {
        this.$data.pageYOffset = window.pageYOffset
      },
      handleResize () {
        this.$data.drawer = false
      }
    },
    mounted () {
      window.setTimeout(() => {
        this.$data.animationShow = true
      }, 1000)
      window.addEventListener('scroll', this.handleScroll)
      window.addEventListener('resize', this.handleResize)
    },
    computed: {
      isTop () {
        return this.pageYOffset <= 60
      }
    },
    data () {
      return {
        pageYOffset: 0,
        drawer: false,
        animationShow: false,
        items: [
          {
            icon: 'language',
            title: 'About Us'
          },
          {
            icon: 'supervisor_account',
            title: 'Partners'
          },
          {
            icon: 'today',
            title: 'Events'
          },
          {
            icon: 'autorenew',
            title: 'ECO System'
          },
          {
            icon: 'extension',
            title: 'IPO Strategy'
          },
          {
            icon: 'theaters',
            title: 'Videos'
          },
          {
            icon: 'record_voice_over',
            title: 'FAQ'
          }
        ],
        title: 'BlockForm'
      }
    }
  }
</script>



